import React from 'react';
import Notice from '../../../../components/notice';
import TableTwoColumns from '../../components/table-two-columns';

class GzipSummaryMetabox extends React.Component {
	constructor(props) {
		super(props);
	}

	scrollTo(element, to, duration) {
		if (duration <= 0) return;
		const difference = to - element.scrollTop;
		const perTick = difference / duration * 10;

		setTimeout(function() {
			window.console.log( element.scrollTop + perTick );
			//element.scrollTop = element.scrollTop + perTick;
			document.body.scrollTop = element.scrollTop + perTick;
			if (element.scrollTop === to) return;
			this.scrollTo(element, to, duration - 10);
		}, 10);
	}

	handleConfigureLinkClick() {
		const fromElement = document.getElementById('wphb-box-gzip-summary');
		const toElement = document.getElementById('wphb-box-gzip-settings');
		this.scrollTo( fromElement, toElement.offsetTop - 20, 600 );
	}

	/**
	 * Show notice.
	 *
	 * @param {string} type  Notice type.
	 * @returns {React.Component}
	 */
	showNotice(type = 'status') {
		let noticeType = 'success';
		let noticeText = <p>GZip compression is currently active. Good job!</p>;

		if ( 'externalProblem' === type && this.props.externalProblem ) {
			noticeType = 'error';
			noticeText = ( // TODO: span needs to be removed
				<span>
					<p>Gzip is not working properly:</p>
					<ul>
						<li>- Your server may not have the "deflate" module enabled (mod_deflate for Apache,
							ngx_http_gzip_module for NGINX).</li>
						<li>- Contact your host. If deflate is enabled, ask why all .htaccess or nginx.conf
							compression rules are not being applied.</li>
					</ul>
					<p>If re-checking and restarting does not resolve, please check with your host or
						<a href="#" target="_blank">open a support ticket with us</a>.</p>
				</span>
			);

			return <Notice type={ noticeType } text={ noticeText } />
		}

		if ( 'status' === type ) {
			if ( 0 < this.props.inactiveTypes ) {
				noticeType = 'warning';
				noticeText = <p>{ this.props.inactiveTypes } of your compression types are inactive.
					<a href="#" onClick={ () => this.handleConfigureLinkClick() } id="configure-gzip-link">Configure</a> compression for all files types below.</p>;
			}

			return <Notice type={ noticeType } text={ noticeText } />
		}
	}

	render() {
		const columns = [{
			header: 'File type',
			accessor: 'type'
		}, {
			header: 'Current status',
			accessor: 'status'
		}];

		return (
			<div id="wphb-box-gzip-summary" className="sui-box box-gzip-summary">
				<div className="sui-box-header">
					<h3 className="sui-box-title">Summary</h3>
					<div className="sui-actions-right">
						<button className="sui-button sui-button-ghost" onClick={ () => this.props.onClick() }>
							Re-Check Status
						</button>
					</div>
				</div>
				<div className="sui-box-body">
					{ this.showNotice('externalProblem') }

					<p>Gzip compresses your web pages and style sheets before sending them over to the browser. This
						drastically reduces transfer time since the files are much smaller.</p>

					{ this.showNotice() }

					<TableTwoColumns columns={ columns } data={ this.props.gzipStatus } />
				</div>
			</div>
		);
	}
}

export default GzipSummaryMetabox;