import React from 'react';

function TableTwoColumns( { columns, data } ) {
	const tableHeader = columns.map((column) => {
		const className = 'wphb-caching-summary-heading-' + column.accessor;
		return <div className={className}>{column.header}</div>
	});

	const tableRows = data.map((row) => {
		const itemClassName = 'wphb-filename-extension wphb-filename-extension-' + row.type.toLowerCase();
		const tagClassName = 'sui-tag sui-tooltip sui-tooltip-top-left sui-tag-' + ( row.status ? 'success' : 'warning' );
		const tooltip = 'Gzip compression is ' + ( row.status ? 'active' : 'inactive' ) + ' for ' + row.type;

		return (
			<div className="table-row">
				<div className="wphb-caching-summary-item-type">
					<span className={itemClassName}>{row.accessor}</span>
					{row.type}
				</div>

				<div>
					<span className={tagClassName} data-tooltip={tooltip}>
						{row.status ? 'Active' : 'Inactive'}
					</span>
				</div>
			</div>
		);
	});

	return (
		<div className="wphb-border-frame two-columns">
			<div className="table-header">
				{tableHeader}
			</div>

			{tableRows}
		</div>
	);
}

export default TableTwoColumns;
